import { BrowserRouter, Route } from "react-router-dom";
import "./App.css";
import BookingCar from "./pages/BookingCar";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Register from "./pages/Register";
import { Redirect } from "react-router";

import "antd/dist/antd.css";

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Route path="https://cars4rents.herokuapp.com/" exact component={Home} />
        <Route path="https://cars4rents.herokuapp.com/login" exact component={Login} />
        <Route path="https://cars4rents.herokuapp.com/register" exact component={Register} />
        <ProtectedRoute path="/booking/:carid" exact component={BookingCar} />
      </BrowserRouter>
    </div>
  );
}

export default App;

export function ProtectedRoute(props) {
  if (localStorage.getItem("user")) {
    return <Route {...props} />;
  } else {
    return <Redirect to="/login" />;
  }
}
